import { REQUEST_SUCCEEDED_CODE } from '@/common/constants';
import { request } from '@/utils/request';
/**
 * commentItem 评论组件
 * @description: 单个评论组件
 * @Author: 穆兰
 * @Date: 2022/1/4
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/7
 * @property {Object} [commentData] - 评论的数据
 * @property {Boolean} [needRefresh] - 是否需要刷新
 * @property {Number} [authorId] - 评论所属动态作者的Id
 * @property {Boolean} [needShowAll] - 是否需要展示全部回复
 * @property {Boolean} [replyAlign] - 回复是否与评论对齐
 * @property {Boolean} [showReplyCount] - 是否显示回复计数
 * @event {Function} onCommentClick - 点击评论主体
 * @event {Function} onCommentMoreClick - 点击评论的更多按钮
 * @event {Function} onReplyClick - 点击评论下的某个回复
 * @event {Function} onReplyMoreClick - 点击评论下的某个回复的更多按钮
 * @event {Function} onShowAllClick - 点击显示更多按钮时
 */
export default {
    name: "comment-item",
    props: {
        commentData: {
            type: Object,
            required: true
        },
        needRefresh: {
            type: Boolean,
            default: false
        },
        authorId: {
            type: Number,
            default: 0
        },
        needShowAll: {
            type: Boolean,
            default: false
        },
        replyAlign: {
            type: Boolean,
            default: false
        },
        showReplyCount: {
            type: Boolean,
            default: false
        }
    },
    watch: {
        needRefresh(newVal) {
            console.log("needRefresh", newVal);
            if (newVal) {
                this.refreshReply();
            }
        }
    },
    data() {
        return {
            replyCommentDatas: [],
            likeSum: 0,
            isLiked: false,
            showAll: true,
            replySum: 0
        };
    },
    async created() {
        this.isLiked = this.commentData.isLiked;
        this.likeSum = this.commentData.likeSum;
        let replyCommentDatas = await this.getCommentReply(this.commentData.id);
        this.replySum = replyCommentDatas.length;
        if (this.replySum <= 3 || this.needShowAll) {
            this.showAll = true;
            this.replyCommentDatas = replyCommentDatas;
        }
        else {
            this.showAll = false;
            this.replyCommentDatas = replyCommentDatas.splice(0, 3);
        }
    },
    methods: {
        /**
         * @description 点击主体
         */
        onClick() {
            this.$emit('onCommentClick');
        },
        /**
         * @description 点击更多按钮
         */
        onMoreClick() {
            this.$emit('onCommentMoreClick');
        },
        /**
         * @description 点击回复的主体
         * @param {Object} item
         * @param {Object} index
         */
        onReplyClick(item, index) {
            this.$emit('onReplyClick', this.commentData, item, index);
        },
        /**
         * @description 点击回复的更多按钮
         * @param {Object} item
         * @param {Object} index
         */
        onReplyMoreClick(item, index) {
            this.$emit('onReplyMoreClick', this.commentData, item, index);
        },
        /**
         * @description 点击展示全部的按钮
         */
        onShowAllClick() {
            this.$emit("onShowAllClick");
        },
        /**
         * @description 点击喜欢按钮
         */
        async onLikeButtonClick() {
            if (this.isLiked == 0) {
                let res = await this.likeThisComment(this.commentData.id);
                if (res) {
                    this.likeSum = this.likeSum + 1;
                    this.isLiked = 1;
                }
            }
            else {
                let res = await this.CancelikeThisComment(this.commentData.id);
                if (res) {
                    this.likeSum = this.likeSum - 1;
                    this.isLiked = 0;
                }
            }
        },
        /**
         * @description 刷新评论下的回复
         */
        async refreshReply() {
            let replyCommentDatas = await this.getCommentReply(this.commentData.id);
            this.replySum = replyCommentDatas.length;
            if (this.replySum <= 3 || this.needShowAll) {
                this.showAll = true;
                this.replyCommentDatas = replyCommentDatas;
            }
            else {
                this.showAll = false;
                this.replyCommentDatas = replyCommentDatas.splice(0, 3);
            }
        },
        /**
         * @description 给这个评论点赞
         * @param {Number} Id 评论的id
         */
        async likeThisComment(id) {
            let res = await request({
                data: {
                    method: 'PUT',
                    group: 'activity/comment',
                    action: `${id}/like`,
                    data: {
                        id: id
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                console.log(res.data);
                return true;
            }
            else {
                return false;
            }
        },
        /**
         * @description 取消这条评论的点赞
         * @param {Number} Id 评论的id
         * @return {ReplyCommentDataItem[]}
         */
        async CancelikeThisComment(id) {
            let res = await request({
                data: {
                    method: 'DELETE',
                    group: 'activity/comment',
                    action: `${id}/like/remove`,
                    data: {
                        id: id
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                console.log(res.data);
                return true;
            }
            else {
                console.log(res);
                return false;
            }
        },
        /**
         * @description  获取这条评论的回复
         * @param {Number} Id 评论的id
         */
        async getCommentReply(id) {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'activity/comment',
                    action: `${id}/responseList`,
                    data: {
                        id: id
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                return res.data.data;
            }
            else {
                return [];
            }
        }
    }
};
